import myIcon from '../../components/my-login/my-icon.vue';
import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue';
export default {
	components:{
		uniStatusBar,myIcon
	},
	data(){
		return{
			zh:false,
			disable:true,
			loading:false,
			userName:'',//昵称/手机号/邮箱
			userPassword:'',//密码
			phone:'',//手机号
			yzm:'',//验证码
			setTimer:0
		}
	},
	watch:{//监听属性
		userName(val){//监听昵称、手机号、邮箱输入框的内容
			this.che();
			console.log(val)
		},
		userPassword(val){//监听密码的输入框的内容
			this.che();
		},
		phone(val){//监听手机号输入框的内容
			this.che();
		},
		yzm(val){//监听验证码输入框的内容
			this.che();
		}
		
	},
	methods:{
		isPhone(){//封装验证手机号的正则
			var phone=/^1[3456789]\d{9}$/;
			return phone.test(this.phone);
		},
		setTime(){//点击获取验证码
			if(this.setTimer>0){//当点击获取验证码后，在点击显示提示
				uni.showToast({
					title:'不能重复获取',
					icon:'none'
				})
				return false;
			}
			if(!this.isPhone()){//调用手机号正则
				uni.showToast({
					title:'手机号码格式不对',
					icon:'none'
				})
				return false;
			}
			this.setTimer=60;
			let tt=setInterval(()=>{
				this.setTimer--;
				if(this.setTimer<1){//当到0s的时候
					clearInterval(tt);//清除定时器
					this.setTimer=0;//赋值为0
				}
			},1000)
		},
		back(){//点击返回上一页
			uni.navigateBack({
				delta:1
			})
		},
		sub(){//登录
			if(!this.isPhone()){//调用手机号正则
				uni.showToast({
					title:'手机号码格式不对',
					icon:'none'
				})
				return false;
			}
		},
		che(){
			if((this.userName&&this.userPassword)||(this.phone&&this.yzm)){
				this.disable=false;//有值的时候，按钮变色
				return false;
			}
			this.disable=true;
		},
		init(){//初始化，让点击验证码时，输入框的值为空
			this.userName='';
			this.userPassword='';
			this.phone='';
			this.yzm='';
		},
		yzm_one(){//点击验证码登录，显示布局
			this.init();//调用，切换时触发
			this.zh=!this.zh;
		}
	}
}
